
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
directives: {
TransferDom
},
components: {
XHeader,
Group,
Cell,
Search,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
created: function () {
this.getAuthList();
},
methods: {
getAuthList: function () {
this.BaseHttp("./auths", {
searchText: this.searchText
}, (response) => {
this.authList = response;
}, (errorMsg) => {
console.log(errorMsg);
});
},
openAuthCommitHistory: function (authObj) {

let routerMap = {
name: 'AuthCommitLog',
params: {
authId: authObj.id,
authName: authObj.auth
}
}

this.$router.push(routerMap);
},
showAddAuth: function () {
const _this = this
this.$vux.confirm.prompt('svn', {
title: '',
onConfirm (msg) {

if (_this.$ISJS.not.existy(msg) || _this.$ISJS.empty(msg)) {
_this.showAlert("");
return;
}

_this.BaseHttp("./addAuth", {
authName: msg
}, (response) => {

_this.getAuthList();
}, (errorMsg) => {
console.log(errorMsg);
});
}
})
},
onDeleteAuthClicked: function (authObj) {
this.showConfirm("",
()=> {
console.log("del " + authObj.auth);
this.BaseHttp("./delAuth", {
authId: authObj.id
}, (response) => {

_this.getAuthList();
}, (errorMsg) => {
console.log(errorMsg);
});
})
}
},
data() {
return {
searchText: "",
authList: []
}
}
}



