
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'

import { format, subDays } from 'date-fns'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Group,
Cell,
XButton,
Flexbox,
FlexboxItem,
Divider,
Calendar,
Badge,
Popup,
Countup,
FormPreview
},
created: function() {
this.authId = this.$route.params.authId;
this.authName = this.$route.params.authName;

this.startDate = this.aWeekAgoFEnd;
},
mounted: function() {},
computed: {

aWeekAgoFEnd: function() {
var tmpdate = format(this.endDate, 'YYYY-MM-DD');
var weekAgoDay = subDays(tmpdate, 7);
var weekAgoDayStr = format(weekAgoDay, 'YYYY-MM-DD');

return weekAgoDayStr;
},
allCodeLines: function() {

var lines = 0;
for(var index in this.commitLogList) {
lines += (this.commitLogList[index].code_lines);
}
return lines;
}
},
methods: {
getCommitLogs: function() {
if(this.$ISJS.not.existy(this.authId) || this.$ISJS.empty(this.authId)) {
this.showConfirm(" ,", () => {
this.$router.push("/auths");
});

return;
}
this.isLoadingHttp = true;
this.BaseHttp("./commitLogs", {
startDate: this.startDate,
endDate: this.endDate,
auth: this.authName
}, (response) => {
this.isLoadingHttp = false;
this.commitLogList = response;
}, (errorMsg) => {
this.isLoadingHttp = false;
console.log(errorMsg);
this.showAlert(errorMsg);
});

},
getFormListData: function(itemObj) {
var retList = [];

retList.push({
label: '',
value: itemObj.commit_time
});

retList.push({
label: '',
value: itemObj.commit_log
});

return retList;

},
onItemClicked: function(item) {
this.clickedItemDetail = item;
this.isShowItemPopup = true;
},
onCloseItemPopup: function() {
this.clickedItemDetail = {};
this.isShowItemPopup = false;
}
},
data() {
return {
isLoadingHttp: false,
authId: 0,
authName: "",
startDate: "",
endDate: format(new Date(), 'YYYY-MM-DD'), // TODAY
commitLogList: [],
clickedItemDetail: {},
isShowItemPopup: false
}
}
}



